/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable react/no-unescaped-entities */
/* eslint-disable @typescript-eslint/no-unused-vars */
"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { Users, Calendar, Home, User, Loader2, X, Check } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Checkbox } from "@/components/ui/checkbox"
import { Label } from "@/components/ui/label"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { cn } from "@/lib/utils"
import { format } from "date-fns"
import { fr } from "date-fns/locale"
import { Calendar as CalendarComponent } from "@/components/ui/calendar"
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from "@/components/ui/command"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"

interface Group {
  idGroupe: string
  nomGroupe: string
  nbEtudiants: number
}

interface Jury {
  idJury: number
  nom: string
  valideDeliberation: number
}

interface Room {
  id: string
  name: string
}

interface FormData {
  date: Date | undefined
  time: string
  location: string
  jury: string[]
  group: string[] // Array for multiple groups
}

interface Toast {
  message: string
  type: "success" | "error" | "info"
}

const INITIAL_FORM_STATE: FormData = {
  date: undefined,
  time: "",
  location: "",
  jury: [],
  group: [],
}

// Static rooms (as requested)
const rooms: Room[] = [
  { id: "Salle 1", name: "Salle 1" },
  { id: "Salle 2", name: "Salle 2" },
  { id: "Salle 3", name: "Salle 3" },
  { id: "Salle 4", name: "Salle 4" },
  { id: "Salle 5", name: "Salle 5" },
  { id: "Salle 6", name: "Salle 6" },
  { id: "Salle 7", name: "Salle 7" },
  { id: "Salle 8", name: "Salle 8" },
  { id: "Salle 9", name: "Salle 9" },
]

const timeSlots = [
  "08:00",
  "08:30",
  "09:00",
  "09:30",
  "10:00",
  "10:30",
  "11:00",
  "11:30",
  "12:00",
  "12:30",
  "13:00",
  "13:30",
  "14:00",
  "14:30",
  "15:00",
  "15:30",
  "16:00",
  "16:30",
  "17:00",
  "17:30",
  "18:00",
]

export default function PlanifierSoutenance() {
  const [formData, setFormData] = useState<FormData>(INITIAL_FORM_STATE)
  const [groups, setGroups] = useState<Group[]>([])
  const [availableGroups, setAvailableGroups] = useState<Group[]>([])
  const [jurys, setJurys] = useState<Jury[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [toast, setToast] = useState<Toast | null>(null)
  const [openTimePopover, setOpenTimePopover] = useState(false)
  const [mode, setMode] = useState<"manual" | "aleatoire" | "semi-aleatoire">("manual")
  const [isSubmitting, setIsSubmitting] = useState(false)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)
        const [groupsResponse, jurysResponse] = await Promise.all([
          fetch("http://localhost:5000/api/groups"), // Your group API
          fetch("http://localhost:5000/api/jurys"),
        ])

        if (!groupsResponse.ok || !jurysResponse.ok) {
          throw new Error("Failed to fetch data")
        }

        const [groupsData, jurysData] = await Promise.all([groupsResponse.json(), jurysResponse.json()])

        setGroups(groupsData)
        setAvailableGroups(groupsData)
        setJurys(jurysData)
      } catch (error) {
        setError("Erreur lors du chargement des données. Veuillez réessayer.")
        console.error("Error fetching data:", error)
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [])

  useEffect(() => {
    setFormData(INITIAL_FORM_STATE)
    setAvailableGroups(groups)
  }, [mode, groups])

  useEffect(() => {
    if (toast) {
      const timer = setTimeout(() => setToast(null), 3000)
      return () => clearTimeout(timer)
    }
  }, [toast])

  const onAddSoutenance = async () => {
    if (
      !formData.date ||
      !formData.time ||
      !formData.location ||
      formData.jury.length === 0 ||
      formData.group.length === 0
    ) {
      setToast({
        type: "error",
        message: "Veuillez remplir tous les champs obligatoires",
      })
      return
    }

    try {
      setIsSubmitting(true)
      const response = await fetch("http://localhost:5000/api/soutenance", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          date: formData.date.toISOString(),
          time: formData.time,
          location: formData.location,
          juryIds: formData.jury.map((id) => Number.parseInt(id, 10)),
          groupIds: formData.group.map((id) => Number.parseInt(id, 10)),
          status: "Pending",
        }),
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.message || "An error occurred")
      }

      setToast({ type: "success", message: "Soutenance planifiée avec succès !" })
      setFormData(INITIAL_FORM_STATE)
      setAvailableGroups(groups)
    } catch (error) {
      setToast({
        type: "error",
        message: error instanceof Error ? error.message : "Échec de l'ajout de la soutenance",
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleJuryChange = async (juryId: string, checked: boolean) => {
    let newJury = [...formData.jury];
  
    // Update jury selection based on mode
    if (mode === "aleatoire") {
      // In aleatoire mode, allow only one jury.
      if (checked) {
        if (newJury.length === 0) {
          newJury = [juryId];
        } else {
          setToast({ type: "error", message: "En mode aléatoire, vous ne pouvez sélectionner qu'un jury." });
          return;
        }
      } else {
        newJury = newJury.filter((id) => id !== juryId);
      }
    } else if (mode === "semi-aleatoire") {
      // In semi-aleatoire mode, allow up to 3 jurys.
      if (checked) {
        if (newJury.length < 3) {
          newJury.push(juryId);
        } else {
          setToast({ type: "error", message: "Vous ne pouvez sélectionner que 3 jurys maximum." });
          return;
        }
      } else {
        newJury = newJury.filter((id) => id !== juryId);
      }
    } else {
      // Manual mode (similar to semi-aleatoire, but no auto-selection)
      if (checked) {
        if (newJury.length < 3) {
          newJury.push(juryId);
        } else {
          setToast({ type: "error", message: "Vous ne pouvez sélectionner que 3 jurys maximum." });
          return;
        }
      } else {
        newJury = newJury.filter((id) => id !== juryId);
      }
    }
  
    // For manual mode, simply update the jury selection and available groups remain as the full list.
    if (mode === "manual") {
      setFormData({ ...formData, jury: newJury });
      setAvailableGroups(groups);
      return;
    }
  
    // For both aleatoire and semi-aleatoire, update the form's jury field.
    // (We don't reset groups here because we want to accumulate auto-selected groups.)
    setFormData({ ...formData, jury: newJury });
  
    // If there is at least one jury selected, fetch its associated groups.
    if (newJury.length > 0) {
      try {
        if (mode === "aleatoire") {
          // In aleatoire mode, use the first selected jury only.
          const response = await fetch(`http://localhost:5000/api/soutenance/jury-groups/${newJury[0]}`);
          if (!response.ok) throw new Error("Failed to fetch jury groups");
          const associatedGroups = await response.json();
          setAvailableGroups(associatedGroups);
          if (associatedGroups.length > 0) {
            setFormData((prev) => ({ ...prev, group: [associatedGroups[0].idGroupe] }));
            setToast({ type: "success", message: "1 groupe automatiquement sélectionné." });
          } else {
            setFormData((prev) => ({ ...prev, group: [] }));
            setToast({ type: "info", message: "Aucun groupe associé à ce jury." });
          }
        } else if (mode === "semi-aleatoire") {
          // For each selected jury, fetch its associated groups.
          const groupPromises = newJury.map((id) =>
            fetch(`http://localhost:5000/api/soutenance/jury-groups/${id}`).then((res) => res.json())
          );
          const groupArrays = await Promise.all(groupPromises);
          // For each jury, select the first group (if available)
          const autoSelectedGroups = groupArrays
            .map((groups) => (groups.length > 0 ? groups[0].idGroupe : null))
            .filter((g): g is string => g !== null);
          // Remove duplicates in case two jurys return the same group.
          const uniqueGroups = Array.from(new Set(autoSelectedGroups));
          // Also update availableGroups to show all groups from the selected jurys.
          const mergedAvailable = groupArrays.flat();
          setAvailableGroups(mergedAvailable);
          setFormData((prev) => ({ ...prev, group: uniqueGroups }));
          if (uniqueGroups.length > 0) {
            setToast({
              type: "success",
              message: `${uniqueGroups.length} groupe(s) automatiquement sélectionné(s) pour ${newJury.length} jury(s).`,
            });
          } else {
            setToast({
              type: "error",
              message: "Aucun groupe associé aux jurys sélectionnés. Sélectionnez manuellement si nécessaire.",
            });
            setFormData((prev) => ({ ...prev, group: [] }));
          }
        }
      } catch (error) {
        setAvailableGroups(groups);
        setToast({ type: "error", message: "Erreur lors de la récupération des groupes." });
      }
    } else {
      // If no jurys are selected, reset available groups.
      setAvailableGroups(groups);
      setFormData((prev) => ({ ...prev, group: [] }));
    }
  };
  

  const handleGroupChange = (groupId: string, checked: boolean) => {
    if (mode !== "manual") return // Only manual mode allows manual group selection
    setFormData((prev) => ({
      ...prev,
      group: checked ? [...prev.group, groupId] : prev.group.filter((id) => id !== groupId),
    }))
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-b from-background to-muted/20">
        <div className="flex flex-col items-center gap-2">
          <Loader2 className="h-12 w-12 animate-spin text-primary" />
          <p className="text-lg font-medium text-muted-foreground">Chargement des données...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen p-8 bg-gradient-to-b from-background to-muted/20">
        <Alert variant="destructive" className="max-w-md">
          <AlertDescription className="text-center">{error}</AlertDescription>
        </Alert>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/20 py-12 px-4 sm:px-6 relative">
      {toast && (
        <div className="fixed top-4 left-1/2 transform -translate-x-1/2 z-50 animate-in fade-in slide-in-from-top duration-300 w-full max-w-md">
          <Alert
            variant={toast.type === "error" ? "destructive" : "default"}
            className={`flex items-center justify-between p-4 text-lg rounded-lg shadow-lg ${
              toast.type === "success"
                ? "bg-green-500 text-white"
                : toast.type === "error"
                  ? "bg-destructive text-destructive-foreground"
                  : "bg-blue-500 text-white"
            }`}
          >
            <AlertDescription className="font-medium">{toast.message}</AlertDescription>
            <Button
              variant="ghost"
              size="icon"
              className="h-6 w-6 hover:bg-transparent text-current opacity-70 hover:opacity-100"
              onClick={() => setToast(null)}
            >
              <X className="h-5 w-5" />
            </Button>
          </Alert>
        </div>
      )}

      <div className="max-w-5xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold bg-gradient-to-r from-primary to-primary/70 bg-clip-text text-transparent">
            Planification des Soutenances
          </h1>
          <p className="mt-3 text-muted-foreground max-w-2xl mx-auto">
            Organisez les soutenances en sélectionnant des groupes, une date, une salle et un jury
          </p>
        </div>

        <Card className="overflow-hidden border-primary/10 shadow-xl">
          <CardHeader className="bg-gradient-to-r from-primary/10 to-primary/5 pb-6">
            <CardTitle className="text-2xl text-center text-primary">Formulaire de Planification</CardTitle>
          </CardHeader>
          <CardContent className="p-6">
            <div className="mb-8">
              <h2 className="text-2xl font-semibold mb-4 text-primary">Mode de Sélection</h2>
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                <div
                  className={`p-4 rounded-lg border-2 transition-all cursor-pointer ${
                    mode === "manual"
                      ? "border-primary bg-primary/10"
                      : "border-muted hover:border-primary/50 hover:bg-muted/50"
                  }`}
                  onClick={() => setMode("manual")}
                >
                  <RadioGroup
                    value={mode}
                    onValueChange={(value) => setMode(value as "manual" | "aleatoire" | "semi-aleatoire")}
                  >
                    <div className="flex items-center space-x-2">
                      <RadioGroupItem value="manual" id="manual" />
                      <Label htmlFor="manual" className="font-medium">
                        Manuel
                      </Label>
                    </div>
                  </RadioGroup>
                  <p className="mt-2 text-sm text-muted-foreground pl-6">Sélection manuelle de tous les éléments</p>
                </div>

                <div
                  className={`p-4 rounded-lg border-2 transition-all cursor-pointer ${
                    mode === "aleatoire"
                      ? "border-primary bg-primary/10"
                      : "border-muted hover:border-primary/50 hover:bg-muted/50"
                  }`}
                  onClick={() => setMode("aleatoire")}
                >
                  <RadioGroup
                    value={mode}
                    onValueChange={(value) => setMode(value as "manual" | "aleatoire" | "semi-aleatoire")}
                  >
                    <div className="flex items-center space-x-2">
                      <RadioGroupItem value="aleatoire" id="aleatoire" />
                      <Label htmlFor="aleatoire" className="font-medium">
                        Aléatoire
                      </Label>
                    </div>
                  </RadioGroup>
                  <p className="mt-2 text-sm text-muted-foreground pl-6">1 jury, 1 groupe automatique</p>
                </div>

                <div
                  className={`p-4 rounded-lg border-2 transition-all cursor-pointer ${
                    mode === "semi-aleatoire"
                      ? "border-primary bg-primary/10"
                      : "border-muted hover:border-primary/50 hover:bg-muted/50"
                  }`}
                  onClick={() => setMode("semi-aleatoire")}
                >
                  <RadioGroup
                    value={mode}
                    onValueChange={(value) => setMode(value as "manual" | "aleatoire" | "semi-aleatoire")}
                  >
                    <div className="flex items-center space-x-2">
                      <RadioGroupItem value="semi-aleatoire" id="semi-aleatoire" />
                      <Label htmlFor="semi-aleatoire" className="font-medium">
                        Semi-Aléatoire
                      </Label>
                    </div>
                  </RadioGroup>
                  <p className="mt-2 text-sm text-muted-foreground pl-6">2-3 jurys, 1 groupe par jury</p>
                </div>
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              {/* Group Selection */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 text-xl text-primary">
                  <Users className="h-6 w-6" />
                  <h3 className="font-semibold">Groupes d'étudiants</h3>
                </div>
                <div className="border rounded-lg p-4">
                  <div className="space-y-2 max-h-60 overflow-auto pr-2 scrollbar-thin scrollbar-thumb-primary/20 scrollbar-track-transparent">
                    {availableGroups.map((group) => (
                      <div
                        key={group.idGroupe}
                        className={`flex items-center space-x-2 p-2 rounded-md transition-colors ${
                          formData.group.includes(group.idGroupe) ? "bg-primary/10" : "hover:bg-muted/50"
                        }`}
                      >
                        <Checkbox
                          id={`group-${group.idGroupe}`}
                          checked={formData.group.includes(group.idGroupe)}
                          onCheckedChange={(checked) => handleGroupChange(group.idGroupe, checked === true)}
                          disabled={mode !== "manual"}
                          className={formData.group.includes(group.idGroupe) ? "text-primary" : ""}
                        />
                        <Label
                          htmlFor={`group-${group.idGroupe}`}
                          className={`text-base cursor-pointer ${
                            formData.group.includes(group.idGroupe) ? "font-medium" : ""
                          }`}
                        >
                          {group.nomGroupe} ({group.nbEtudiants} étudiants)
                        </Label>
                      </div>
                    ))}
                  </div>
                  {formData.group.length > 0 && (
                    <div className="mt-4 p-3 bg-primary/5 rounded-md border border-primary/10">
                      <p className="font-medium text-sm text-primary">Groupes sélectionnés:</p>
                      <div className="flex flex-wrap gap-2 mt-2">
                        {formData.group.map((id) => (
                          <div
                            key={id}
                            className="inline-flex items-center gap-1 bg-primary/15 text-primary rounded-full px-3 py-1 text-sm font-medium shadow-sm"
                          >
                            {availableGroups.find((g) => g.idGroupe === id)?.nomGroupe}
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              </div>

              {/* Date and Time Selection */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 text-xl text-primary">
                  <Calendar className="h-6 w-6" />
                  <h3 className="font-semibold">Date de soutenance</h3>
                </div>
                <div className="border rounded-lg p-4">
                  <div className="grid gap-4">
                    <Popover>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          className={cn(
                            "w-full justify-start text-left font-normal h-12 text-base border-2 hover:bg-muted/50 transition-colors",
                            formData.date ? "border-primary/30 text-foreground" : "text-muted-foreground",
                          )}
                        >
                          <Calendar className="mr-2 h-5 w-5 text-primary" />
                          {formData.date ? format(formData.date, "PPP", { locale: fr }) : "Sélectionner une date"}
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto p-0">
                        <CalendarComponent
                          mode="single"
                          selected={formData.date}
                          onSelect={(date) => setFormData({ ...formData, date })}
                          initialFocus
                        />
                      </PopoverContent>
                    </Popover>

                    <Popover open={openTimePopover} onOpenChange={setOpenTimePopover}>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          className={cn(
                            "w-full justify-start text-left font-normal h-12 text-base",
                            !formData.time && "text-muted-foreground",
                          )}
                        >
                          <Clock className="mr-2 h-4 w-4" />
                          {formData.time ? formData.time : "Sélectionner une heure"}
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-full p-0" align="start">
                        <Command>
                          <CommandInput placeholder="Rechercher une heure..." />
                          <CommandList>
                            <CommandEmpty>Aucune heure trouvée.</CommandEmpty>
                            <CommandGroup className="max-h-60 overflow-auto">
                              {timeSlots.map((time) => (
                                <CommandItem
                                  key={time}
                                  value={time}
                                  onSelect={() => {
                                    setFormData({ ...formData, time })
                                    setOpenTimePopover(false)
                                  }}
                                >
                                  <Check
                                    className={cn("mr-2 h-4 w-4", formData.time === time ? "opacity-100" : "opacity-0")}
                                  />
                                  {time}
                                </CommandItem>
                              ))}
                            </CommandGroup>
                          </CommandList>
                        </Command>
                      </PopoverContent>
                    </Popover>
                  </div>
                </div>
              </div>

              {/* Room Selection */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 text-xl text-primary">
                  <Home className="h-6 w-6" />
                  <h3 className="font-semibold">Salle</h3>
                </div>
                <div className="border rounded-lg p-4">
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button variant="outline" role="combobox" className="w-full justify-between h-12 text-base">
                        {formData.location
                          ? rooms.find((room) => room.id === formData.location)?.name
                          : "Sélectionner une salle"}
                        <Home className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-full p-0" align="start">
                      <Command>
                        <CommandInput placeholder="Rechercher une salle..." />
                        <CommandList>
                          <CommandEmpty>Aucune salle trouvée.</CommandEmpty>
                          <CommandGroup className="max-h-60 overflow-auto">
                            {rooms.map((room) => (
                              <CommandItem
                                key={room.id}
                                value={room.name}
                                onSelect={() => setFormData({ ...formData, location: room.id })}
                              >
                                <Check
                                  className={cn(
                                    "mr-2 h-4 w-4",
                                    formData.location === room.id ? "opacity-100" : "opacity-0",
                                  )}
                                />
                                {room.name}
                              </CommandItem>
                            ))}
                          </CommandGroup>
                        </CommandList>
                      </Command>
                    </PopoverContent>
                  </Popover>
                </div>
              </div>

              {/* Jury Selection */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 text-xl text-primary">
                  <User className="h-6 w-6" />
                  <h3 className="font-semibold">Jury (max. 3)</h3>
                </div>
                <div className="border rounded-lg p-4">
                  <div className="space-y-4">
                    <div className="grid grid-cols-1 gap-3">
                      {jurys.map((jury) => (
                        <div
                          key={jury.idJury}
                          className={`flex items-center space-x-2 rounded-md border p-3 transition-colors ${
                            formData.jury.includes(jury.idJury.toString())
                              ? "bg-primary/10 border-primary/30"
                              : "hover:bg-muted/50"
                          }`}
                        >
                          <Checkbox
                            id={`jury-${jury.idJury}`}
                            checked={formData.jury.includes(jury.idJury.toString())}
                            onCheckedChange={(checked) => handleJuryChange(jury.idJury.toString(), checked === true)}
                            disabled={
                              mode === "aleatoire" &&
                              formData.jury.length === 1 &&
                              !formData.jury.includes(jury.idJury.toString())
                            }
                            className={formData.jury.includes(jury.idJury.toString()) ? "text-primary" : ""}
                          />
                          <Label
                            htmlFor={`jury-${jury.idJury}`}
                            className={`flex-1 cursor-pointer text-base ${
                              formData.jury.includes(jury.idJury.toString()) ? "font-medium" : ""
                            }`}
                          >
                            {jury.nom}
                          </Label>
                        </div>
                      ))}
                    </div>

                    {formData.jury.length > 0 && (
                      <div className="mt-4 p-3 bg-primary/5 rounded-md border border-primary/10">
                        <p className="font-medium text-sm text-primary">Jurys sélectionnés:</p>
                        <div className="flex flex-wrap gap-2 mt-2">
                          {formData.jury.map((id) => {
                            const juryName = jurys.find((jury) => jury.idJury.toString() === id)?.nom
                            return (
                              <div
                                key={id}
                                className="inline-flex items-center gap-1 bg-primary/15 text-primary rounded-full px-3 py-1 text-sm font-medium shadow-sm"
                              >
                                <User className="h-3 w-3" />
                                {juryName}
                              </div>
                            )
                          })}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>

            <div className="mt-8 flex justify-center">
              <Button
                onClick={onAddSoutenance}
                className="h-12 px-8 text-lg font-medium rounded-full bg-gradient-to-r from-primary to-primary/80 hover:from-primary/90 hover:to-primary shadow-md hover:shadow-lg transition-all"
                disabled={
                  !formData.date ||
                  !formData.time ||
                  !formData.location ||
                  formData.jury.length === 0 ||
                  formData.group.length === 0 ||
                  isSubmitting
                }
              >
                {isSubmitting ? (
                  <>
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                    Planification en cours...
                  </>
                ) : (
                  <>
                    <Calendar className="mr-2 h-5 w-5" />
                    Planifier la soutenance
                  </>
                )}
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}

function Clock(props: React.SVGProps<SVGSVGElement>) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <circle cx="12" cy="12" r="10" />
      <polyline points="12 6 12 12 16 14" />
    </svg>
  )
}

